import {
  MobileMentionButton,
  MobileMentionInsertButton,
} from '../components/mentionButtonMobile';
import { MentionPluginConfig } from '../types';

export default function toolbar(mentionConfig: MentionPluginConfig) {
  const mentionLocaleConfig = mentionConfig.locale;
  return {
    buttons: {
      mobile: MobileMentionButton,
      mobileInsert: MobileMentionInsertButton,
    },
    locale: mentionLocaleConfig,
  };
}
